<template>
  <div id="wrapper" class="bg_white">
    <!-- Content -->
    <section id="content">
      <div class="container">
        <div class="check-all">
          <div class="checkbox" v-bind:class="checked"><label><input type="checkbox" id="checkall" v-model="chkAll" v-on:click="allChecked()">   </label></div>
        </div>
      </div>
      <!-- panel -->
      <div class="panel-group agree-panel" id="termsList">
        <div class="panel panel-default">
          <div class="panel-heading">
            <div class="checkbox">
              <label>
  							<input type="checkbox" name="checkbox1" id="checkbox1" v-model="chkBox1" />
  							<a href="" class="collapsed" role="button" data-toggle="collapse"><em>[]</em>  </a>
  						</label>
            </div>
          </div>
          <div class="panel-collapse" id="panel1">
            <ul class="list-group">
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp1');">
                  <label> </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp2');">
                  <label> </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp3');">
                  <label>KCB  </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp4');">
                  <label>    </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp5');">
                  <label> 3  </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
            </ul>
          </div>
        </div>
        <div class="panel panel-default">
          <div class="panel-heading">
            <div class="checkbox">
              <label>
  							<input type="checkbox" name="checkbox2" id="checkbox2" v-model="chkBox2" />
  							<a href="" class="collapsed" role="button" data-toggle="collapse"><em>[]</em>   </a>
  						</label>
            </div>
          </div>
          <div class="panel-collapse" id="panel2">
            <ul class="list-group">
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp6');">
                  <label> </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp7');">
                  <label>   / </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp8');">
                  <label> </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp9');">
                  <label>   </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
            </ul>
          </div>
        </div>
        <div class="panel panel-default">
          <div class="panel-heading">
            <div class="checkbox">
              <label>
  							<input type="checkbox" name="checkbox3" id="checkbox3" v-model="chkBox3" />
  							<a href="" class="collapsed" role="button" data-toggle="collapse"><em>[]</em>    </a>
  						</label>
            </div>
          </div>
          <div class="panel-collapse" id="panel3">
            <ul class="list-group">
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp10');">
                  <label>   </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <!-- //panel -->
      <div class="btn-fixed-bottom" id="next_div">
        <a role="button" id="confirmButton" class="btn btn-lg btn-primary btn-block" v-on:click="confirmedTerms()"></a>
      </div>
    </section>
    <!-- //Content -->
  </div>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";

export default {
  name: "certStep1",
  data() {
    return {
      uuid: "",
      errMsg: "",
      checked: "",
      chkAll: false,
      chkBox1: false,
      chkBox2: false,
      chkBox3: false
    };
  },
  component: {},
  // computed () {
  // },
  beforeCreate() {},
  created() {
    if (Constant.userAgent == "Android") {
      window.Android.setEndApp("Y");
    }
    this.$store.state.title = " (7/7)";
    this.checkUUID();
  },
  beforeMount() {},
  mounted() {
    var _this = this;
    this.getTermsContent;
    $(":checkbox").change(function() {
      Common.affixBottom("hide");
      //  
      if (_this.chkBox1 && _this.chkBox2 && _this.chkBox3) {
        _this.chkAll = true;
        _this.checked = "check";
      } else {
        _this.chkAll = false;
        _this.checked = "";
      }

      if (_this.chkBox1 && _this.chkBox2) {
        Common.affixBottom("show");
      }

      //   parameter 
      if (_this.chkBox3) {
        _this.$store.state.user.isEventPush = true;
      }
    });
  },
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    // 
    getTermsContent: function() {
      var _this = this;
      var formData = new FormData();
      formData.append("no_person", this.$store.state.user.noPerson);
      formData.append("uuid", this.uuid);
      formData.append("dn", this.$route.params.dn);

      this.$http
        .post("/m/scrap/getTermsContent.json", formData)
        .then(function(response) {
          var result = response.data.result;
          _this.isScrapStList = true;
          if (_this.isScrapFcList && _this.isScrapStList) {
            _this.nextStep();
          }
        })
        .catch(e => {
          this.$toast.center(ko.messages.error);
        });
    },
    // UUID 
    checkUUID: function() {
      if (Constant.userAgent == "iOS") {
        //     
        Jockey.on("resultCheckDevicesUUID", function(param) {
          resultCheckDevicesUUID(uuid);
        });
        Jockey.send("checkDevicesUUID");
      } else if (Constant.userAgent == "Android") {
        window.Android.checkDevicesUUID();
      }
    },
    // UUID  ( )
    resultCheckDevicesUUID: function(uuid) {
      this.uuid = uuid;
    },
    allChecked: function() {
      var _this = this;
      if (_this.chkAll) {
        _this.chkAll = false;
      } else {
        _this.chkAll = true;
      }

      if (_this.chkAll) {
        _this.checked = "check";
        _this.chkBox1 = true;
        _this.chkBox2 = true;
        _this.chkBox3 = true;
      } else {
        _this.checked = "";
        _this.chkBox1 = false;
        _this.chkBox2 = false;
        _this.chkBox3 = false;
      }
    },
    confirmedTerms: function() {
      //if (_this.chkBox1 && _this.chkBox2) {
      this.$router.push({
        name: "scrapLoading",
        params: {
          dn: this.$route.params.dn,
          cn: this.$route.params.cn,
          normalMessage: "  <br>  .",
          smallMessage: " ."
        }
      });
      //}
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



